import React, { Component } from 'react';
import authService from './api-authorization/AuthorizeService'

export class FetchData extends Component {
  static displayName = FetchData.name;

  constructor(props) {
    super(props);
    this.state = { forecasts: [], loading: true, nombre: "" };
  }

  componentDidMount() {
      this.populateWeatherData();
      this.siSoy();
  }

  static renderForecastsTable(forecasts) {
      return (
        //usar "table-striped" en className para alternar grisado de fondo... pero customizada 
      <table className='table table-sm table-dark table-striped table-hover text-light' aria-labelledby="tabelLabel">
        <thead>
          <tr>
            <th>Date</th>
            <th>Temp. (C)</th>
            <th>Temp. (F)</th>
            <th>Summary</th>
          </tr>
        </thead>
        <tbody>
          {forecasts.map(forecast =>
            <tr key={forecast.date}>
              <td>{forecast.date}</td>
              <td>{forecast.temperatureC}</td>
              <td>{forecast.temperatureF}</td>
              <td>{forecast.summary}</td>
            </tr>
          )}
        </tbody>
      </table>
    );
  }

  render() {
    let contents = this.state.loading
      ? <p><em>Loading...</em></p>
      : FetchData.renderForecastsTable(this.state.forecasts);

    return (
      <div>
            <h1 id="tabelLabel" >Weather forecast { this.state.nombre }</h1>
        <p>This component demonstrates fetching data from the server.</p>
        {contents}
      </div>
    );
  }

  async populateWeatherData() {
    const token = await authService.getAccessToken();
      const response = await fetch('manzana', {
          headers: !token ? {} : { 'Authorization': `Bearer ${token}` }
      });
    const data = await response.json();
    this.setState({ forecasts: data, loading: false });
  }

    async siSoy() {
        const token = await authService.getAccessToken();
        const response = await fetch('sisoy', {
            headers: !token ? {} : { 'Authorization': `Bearer ${token}` }
        });
        const elnombre = await response.text();
        this.setState({ nombre: elnombre });
        console.log(elnombre);

    }




}
